import React from "react"
import {graphql} from "gatsby"
import Layout from "../Components/Layout";
import {SEO} from "theme-seo";

const Feature = ({data, pageContext}) => {
  const feature = data.markdownRemark
  return (
    <Layout>
      <SEO title={pageContext.title} description={feature.frontmatter.description} 
      />
      <main className="text-right w-10/12 lg:w-3/5 mx-auto">
        <div className="WordpressStyles markdownBlog">
        <h1 className="text-gray-700 xl:pt-12">{feature.frontmatter.title}</h1>
          <div className="lg:py-2">
            <div className="text-gray-600 lg:px-8" dangerouslySetInnerHTML={{__html: feature.html}}/>
          </div>
        </div>
        <div className="p-16">
          {feature.frontmatter.videoName && <video className="w-full mx-auto"
                          src={pageContext.videoUrl + feature.frontmatter.videoName}
                          controls
                          preload="none"
                    />}
        </div>
      </main>
    </Layout>
  )
}

export default Feature

export const query = graphql`
  query($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      id
      html
      frontmatter {
        title
        description
        videoName
        icon
      }
    }

    allFile(filter: { sourceInstanceName: { eq: "feature" }, extension:{ne:"md"}}) {
      nodes {
        name 
        publicURL
        extension
      }
    }
  }
`
